.chat-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: #f5f5f5;
  font-family: 'Inter', sans-serif;
  justify-content: center;
  align-items: center;
}

.chat-box {
  width: 90%;
  max-width: 600px;
  height: 70vh;
  background: white;
  border-radius: 12px;
  padding: 20px;
  overflow-y: auto;
  box-shadow: 0 4px 15px rgba(0,0,0,0.1);
}

.chat-message {
  display: flex;
  margin: 10px 0;
}

.chat-message.user {
  justify-content: flex-end;
}

.chat-message.bot {
  justify-content: flex-start;
}

.bubble {
  max-width: 75%;
  padding: 12px 18px;
  border-radius: 20px;
  font-size: 16px;
  line-height: 1.4;
}

.chat-message.user .bubble {
  background-color: #007bff;
  color: white;
  border-bottom-right-radius: 4px;
}

.chat-message.bot .bubble {
  background-color: #e9ecef;
  color: #333;
  border-bottom-left-radius: 4px;
}

.chat-input {
  display: flex;
  width: 90%;
  max-width: 600px;
  margin-top: 15px;
}

.chat-input input {
  flex-grow: 1;
  padding: 12px 16px;
  border: 1px solid #ccc;
  border-radius: 8px;
  font-size: 16px;
}

.chat-input button {
  margin-left: 10px;
  background-color: #007bff;
  border: none;
  color: white;
  padding: 12px 20px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 16px;
  transition: background 0.3s;
}

.chat-input button:hover {
  background-color: #0056b3;
}
